import React, { useState } from 'react'
import { homeWorkReducer } from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'

export type UserType = {
    _id: number
    name: string
    age: number
}

const initialPeople: UserType[] = [
    { _id: 0, name: 'Кот', age: 3 },
    { _id: 1, name: 'Александр', age: 66 },
    { _id: 2, name: 'Коля', age: 16 },
    { _id: 3, name: 'Виктор', age: 44 },
    { _id: 4, name: 'Дмитрий', age: 40 },
    { _id: 5, name: 'Ирина', age: 55 },
]

function HW8() {
    const [people, setPeople] = useState<UserType[]>(initialPeople)

    const finalPeople = people.map((p: UserType) => (
        <div key={p._id}>
            <span>{p.name}</span> <span>{p.age}</span>
        </div>
    ))

    const sortUp = () => {
        console.log('im here')

        setPeople(homeWorkReducer(initialPeople, { type: 'sort', payload: 'up' }))
    }
    const sortDown = () => {
        console.log('im here')

        setPeople(homeWorkReducer(initialPeople, { type: 'sort', payload: 'down' }))
    }
    const ageCheck = () => {
        console.log('im here')

        setPeople(homeWorkReducer(initialPeople, { type: 'check', payload: 18 }))
    }

    return (
        <div>
            <hr />
            homeworks 8

            {/*should work (должно работать)*/}
            {finalPeople}

            <div>
                <SuperButton text={'sort up'} onClick={sortUp} />
                <SuperButton text={'sort down'} onClick={sortDown} />
                <SuperButton text={'check 18'} onClick={ageCheck} />
            </div>

            <hr />
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativePeople/>*/}
            <hr />
        </div>
    )
}

export default HW8
